.reacti-page {
    padding: 20px;
    max-width: 800px;
    margin: auto;
    font-family: sans-serif;
    line-height: 1.6;
  }
  
  .reacti-page h1 {
    color: #007bff;
    margin-bottom: 10px;
  }
  
  .reacti-page h2 {
    color: #28a745;
    margin-top: 20px;
  }
  
  .reacti-page ul {
    list-style-type: disc;
    padding-left: 20px;
  }
  
  .reacti-page li {
    margin-bottom: 5px;
  }
  